.popup__container {
    position: relative;
    max-width: 282px;
    width: 100%;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
}

@media screen and (min-width: 1280px) {
    .popup__container {
    max-width: 430px;
    }
}

