.profile {
    max-width: 880px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin: 0 auto;
    padding: 38px 0 0;
}

@media screen and (min-width: 1280px) {
    .profile {
    flex-direction: row;
    align-items: center;
    }
}